.recommended {
  &__products {
    display: grid;
    row-gap: 40px;
    opacity: 1;

    @include page-grid;

    @include on-tablet {
      row-gap: 56px;
    }
  }

  &__product {
    grid-column: 1 / -1;

    @include move(1, 3);

    @include on-desktop {
      grid-column: span 4;
    }
  }
}
